.profile {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  border-radius: 4px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1px 3px 0px rgba(191, 205, 222, 0.75);
  width: 250px;
  margin-bottom: 30px;
}

.description {
  padding-top: 30px;
}

.avatar {
  width: 100px;
  border-radius: 50%;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
}

.name {
  font-size: 18px;
  font-family: 'Lato', sans-serif;
  color: rgb(18, 34, 54);
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 10px;
}

.tag {
  font-size: 14px;
  font-family: 'Lato', sans-serif;
  color: rgb(118, 134, 150);
  line-height: 1.2;
  margin-bottom: 10px;
}

.location {
  composes: tag;
  margin-bottom: 30px;
}

.statsList {
  display: flex;
  padding: 0;
  margin-bottom: 0;
}

.statsItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border: 1px solid rgb(228, 233, 240);
  background-color: rgb(243, 246, 249);
  width: 84px;
  padding: 10px;
}

.label {
  font-size: 12px;
  font-family: 'Lato', sans-serif;
  color: rgb(118, 134, 150);
  line-height: 1.2;
  margin-bottom: 10px;
}

.quantity {
  font-size: 14px;
  font-family: 'Lato', sans-serif;
  color: rgb(31, 51, 73);
  line-height: 1.2;
}
